let lang = navigator.language || navigator.browserLanguage;
lang = lang.toLowerCase();

export function getI18NString (key) {
  if (!i18n[lang]) {
    return key;
  }
  let result = i18n[lang][key];
  if (result === undefined) {
    return key;
  }
  return result;
}
